  overflow-x: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.page-header {
  background-color: #ffffff;
  padding-top: 32px;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 24px;
  overflow: hidden;
}

.page-header--menu-oppened {
  height: 100vh;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header__toggle-container {
  width: 27px;
  height: 19px;
}

.page-header__toggle {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  background: none;
  padding: 0;
  margin: 0;
  border: none;
  cursor: pointer;
}
.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 100%;
  height: 3px;
  background-color: #171717;
  border-radius: 10px;
  transition: 0.5s;
}
.page-header__toggle::after {
  content: "";
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%) rotate(-45deg);
  width: 100%;
  height: 3px;
  background-color: #171717;
  border-radius: 10px;
  transition: 0.5s;
}

.page-header__toggle span {
  display: none;
  width: 100%;
  height: 3px;
  background-color: #171717;
  border-radius: 10px;
}

.page-header__button {
  width: 223px;
  padding: 17px 32px;
}

.page-header__button--oppened {
  position: absolute;
  bottom: 24px;
}

.page-header__toggle--closed {
  height: 19px;
}
.page-header__toggle--closed::before, .page-header__toggle--closed::after {
  transform: none;
}
.page-header__toggle--closed::before {
  top: 0;
  left: 0;
}
.page-header__toggle--closed::after {
  bottom: 0;
  left: 0;
}

.page-header__toggle--closed span {
  display: block;
}

